.sidedrawer {
    height: 100%;
    background: white;
    box-shadow:  1px 0px 7px rgb(12, 12, 12, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width:70%;
    max-width: 400px;
    z-index: 200;
    transform: translateX(-100%);
    transition: transform 0.3s ease-out
}

.sidedrawer.open {
    transform: translateX(0);
}
.sidedrawer section  {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.listDiv {
    padding: 1rem;
}




.sidedrawer a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem;
}
.sidedrawer a:hover,
.sidedrawer a:active {
    color: gray;
}

.now a {
    color: red;
    text-decoration: none;
    font-size: 1.2rem;
    background-color: blue;
    
}

.notnow a {
    color: black;
    text-decoration: none;
    font-size: 1.2rem;
    padding: 1.2rem;
}
.now {
    background-color: rgba(98, 71, 199, 0.5);
    color: white;
}



.notnow:hover {
    color: white;
    background-color: rgb(127, 127, 206);
    
}


